import React from 'react'
import s from './Users.module.css'
import baseAvatare from '../../redux/avatarIng/rONxYDD5qbI.jpg'
import { NavLink } from 'react-router-dom';
import Paginator from '../common/Paginator/Paginator';

function User({ users, ...props }) {

    return (
        <div>
                <div key={users.id}>
                    <span>
                        <div>
                            <NavLink to={'/profile/' + users.id}>
                                <img className={s.userPhoto} src={users.photos.small != null ? users.photos.small : baseAvatare} alt="avatar" />
                            </NavLink>
                        </div>
                        <div>
                            {users.followed
                                ? <button disabled={props.followingInProgress.some(id => id === users.id)} onClick={() => {
                                    props.unFollowRT(users.id)
                                }}>UnFollow</button>
                                : <button disabled={props.followingInProgress.some(id => id === users.id)} onClick={() => {
                                    props.followRT(users.id)
                                }}>Follow</button>
                            }
                        </div>
                    </span>
                    <span>
                        <span>
                            <div>{users.name}</div>
                            <div>{users.staus}</div>
                        </span>
                        <span>
                            <div>{"item.lockation.country"}</div>
                            <div>{"item.lockation.city"}</div>
                        </span>
                    </span>
                </div>
        </div>
    )
}

export default User;
