import React from 'react'
import { useState } from 'react';
import s from './Paginator.module.css'

function Paginator({ totalItemssCount, pageSize, onPageChenged, currentPage, portionSize = 10 }) {

    let pagesCount = Math.ceil(totalItemssCount / pageSize)
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    };

    let portionCount = Math.ceil(pagesCount / portionSize);
    const [portionNumber, setportionNuber] = useState(1)
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1
    let rightPortionPageNumber = portionSize * portionNumber

    return (
        <div>
            {portionNumber > 1 && <button onClick={() => setportionNuber(portionNumber - 1)}>BACK</button>}
            <div>
                {
                    pages
                        .filter(item => item >= leftPortionPageNumber && item <= rightPortionPageNumber)
                        .map(item => {
                            return (
                                <span key={item} onClick={() => onPageChenged(item)} className={currentPage === item ? s.selectedPage : s.selectedPagehover}>{item}
                                </span>
                            )
                        })
                }
            </div>
            {portionCount > portionNumber && <button onClick={() => setportionNuber(portionNumber + 1)}>NEXT</button>}
        </div>
    )
}

export default Paginator;
