import { Component } from 'react';
import { connect } from 'react-redux';
import { selectedPage } from '../../redux/actions/actionCreator';
import Preeloder from '../common/Preeloder/Preeloder';
import Users from './Users';
import { getUsersThunkCreator, unFollowRT, followRT } from '../../redux/reducers/user-reducer'
import withAuthRedirect from '../../hoc/withAuthRedirect';
import { compose } from 'redux';
import { getPageSize, getUsers, getTotalUsersCount, getCurrentPage, getIsFeching, getFollowingInProgress, getIsAuth } from '../../redux/reducers/user-selectors';


class UsersContainer extends Component {

    componentDidMount() {
        this.props.getUsersThunkCreator(this.props.currentPage, this.props.pageSize)
    };

    onPageChenged = (e) => {
        this.props.selectedPage(e)
        this.props.getUsersThunkCreator(e, this.props.pageSize)
    };

    render() {
        return (
            <>
                {this.props.isFeching ? <Preeloder /> : null}
                <Users
                    totalUsersCount={this.props.totalUsersCount}
                    pageSize={this.props.pageSize}
                    currentPage={this.props.currentPage}
                    onPageChenged={this.onPageChenged}
                    users={this.props.users}
                    unFollowRT={this.props.unFollowRT}
                    followRT={this.props.followRT}
                    isFeching={this.props.isFeching}
                    followingInProgress={this.props.followingInProgress}
                    toggleFollowingProgress={this.props.toggleFollowingProgress}
                />
            </>
        );
    };
};

