import React from 'react'
import Paginator from '../common/Paginator/Paginator';
import User from './User';

function Users({ currentPage, onPageChenged, pageSize, totalUsersCount, users, ...props }) {

    return (
        <div>
            <Paginator
                currentPage={currentPage}
                onPageChenged={onPageChenged}
                totalItemssCount={totalUsersCount}
                pageSize={pageSize} />
            {
                users.map(item => <User
                    key={item.id}
                    users={item}
                    followingInProgress={props.followingInProgress}
                    unFollowRT={props.unFollowRT}
                    followRT={props.followRT}

                />)
            }
        </div>
    )
}

export default Users;
